
body {
    padding: 30px;
}
.slide-enter-active {
    -webkit-animation: slide-in 900ms ease-out forwards;
            animation: slide-in 900ms ease-out forwards;
}
.slide-leave-active {
    -webkit-animation: slide-out 900ms ease-out forwards;
            animation: slide-out 900ms ease-out forwards;
}
@-webkit-keyframes slide-in {
from {
        -webkit-transform: translateY(-30px);
                transform: translateY(-30px);
        opacity: 0;
}
to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
}
}
@keyframes slide-in {
from {
        -webkit-transform: translateY(-30px);
                transform: translateY(-30px);
        opacity: 0;
}
to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
}
}
@-webkit-keyframes slide-out {
from {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
}
to {
        -webkit-transform: translateY(-30px);
                transform: translateY(-30px);
        opacity: 0;
}
}
@keyframes slide-out {
from {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
}
to {
        -webkit-transform: translateY(-30px);
                transform: translateY(-30px);
        opacity: 0;
}
}

